import React, {useRef} from 'react'


// swiper
import Swiper from 'react-id-swiper';
import SwiperCore, { Autoplay, Navigation } from 'swiper';
import 'swiper/css/bundle';

import { getImage } from 'components/getImage'


export default function Slider(props) {
    
    const {item} = props

    const swiperRef = useRef(null);
    SwiperCore.use([Autoplay, Navigation]);

    const params = {
        // slidesPerView: 'auto',
        centeredSlides: true,
        slidesPerView: 4,
        loop: true,
        observer: true,
        spaceBetween: 20,
        on: {
            // realIndexChange: (swiper) => setImageIndex(swiper.realIndex)
        },
        autoplay: {
            delay: 1500,
            disableOnInteraction: false
        },

    }

    

    return (
        <>
            {item.length > 0 &&
                <Swiper {...params} ref={swiperRef}>
                    {item.map((_item) => (<div id={_item.id} className="w-fit   ">
                        <div className='h-full  hover:scale-[1.2] py-[10px] transition '>

                        <img 
                         src={getImage(_item?.image?.url, "assets/coinvesters/1.png")}
                         className="h-full  max-h-[100px]" />
                         </div>
                    </div>))
                    }

                </Swiper>
            }
        </>
    )
}
